<template>
	<div class="main">
		<div class="title">
			查询
		</div>
		<div >
			<el-table
					:data="tableData"
					style="width: 100%"
				>
					<el-table-column prop="date" label="日期" width="180"></el-table-column>
					<el-table-column prop="name" label="姓名" width="180"></el-table-column>
					<el-table-column prop="address"  label="地址"> </el-table-column>
			</el-table>
		</div>
	</div>
</template>
<script>

	export default{
		name:'mianBoard',
		data(){
			return{
				tableData: [{
					date: '2016-05-02',
					name: '王小虎',
					address: '上海市普陀区金沙江路 1518 弄'
				}, {
					date: '2016-05-04',
					name: '王小虎',
					address: '上海市普陀区金沙江路 1517 弄'
				}, {
					date: '2016-05-01',
					name: '王小虎',
					address: '上海市普陀区金沙江路 1519 弄'
				}, {
					date: '2016-05-03',
					name: '王小虎',
					address: '上海市普陀区金沙江路 1516 弄'
				}]
			}
		},
		mounted(){
			// this.onGetData(); 主动调用接口
		},
		methods:{
			onGetData(){
				// 调用数据接口
				this.$api.model().then((res)=>{
					// res -- 返回的数据
					console.log(res);
					
				})
			}
		},
	}
</script>
<style scoped lang="less">
	.main{
		background-color: #f6f7fc;
		height: 100%;
	}
	.title{
		font-size: 18px;
		padding: 10px 0;
	}
	.el-pagination{
		text-align: right;
		margin-top: 10px;
	}
</style>


